* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 10rem;
  font-size: 10px;
  color: white;
  background: linear-gradient(113.49deg, #267fd3 -30.3%, #04111d 58.12%);
  font-family: "Poppins", sans-serif;
}

.font2 {
  font-family: "Playfair Display", serif;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 120%;
}

p,
h4,
h5 {
  margin: 0;
  line-height: 180%;
}

.container {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.onboard-container {
  box-shadow: 10px 10px 30px rgba(0, 0, 0, .1);
  border-radius: .7em;
  padding: 4em;
  background: white;
  min-width: 500px;
  text-align: center;
}

.onboard {
  background-color: rgb(76, 0, 255);
  color: white;
  display: inline-block;
  text-decoration: none;
  padding: .7em;
  text-align: center;
  font-size: 1.3rem;
  border-radius: .3em;
  margin-top: 2em;
}

p {
  font-size: 1.125rem;
}

.loader {
  width: 200px;
  height: 200px;
  margin: 0 auto;
  display: none;
}

.account {
  background: rgb(233, 233, 233);
  padding: .3em;
  border-radius: .3em;
  position: relative;
  display: inline-block;
  text-indent: 1.2em;
}

.account::before {
  position: absolute;
  content: "";
  background: green;
  width: .7em;
  height: .7em;
  border-radius: 50%;
  left: .5em;
  top: .6em;
}

.up {
  position: absolute;
  top: 2em;
  right: 2em;
  width: 10em;
  display: none;
}

.confetti {
  position: absolute;
  width: 20em;
  z-index: 2;
  display: none;
}

